.chart-bar{
    width: 1rem;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.chart-bar__inner{
    height: 100%;
    width: 100%;
    border: 1px solid #313131;
    border-radius: 12px;
    background-color: #ffffff;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.chart-bar__fill{
    background-color: #ffb300;
    width: 100%;
    transition: all 1s ease-out;
}

.chart-bar__label{
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
}