
.new-expense__controls{
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
    justify-content: center;
}

.new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}

.new-expense__control input{
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 2px solid #ffb300;
    width: 20rem;
    max-width: 100%;
}

.form-display__hidden{
display: none;
}

