@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  /* background-color: #fff; */
  /* background-image: url('./background1.jpg');
  background-size: cover; */
  background-color: #000000;
}


.expenses{
    padding: 1rem 2rem;
    background-color:  rgb(0, 0, 0);
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    /* border-radius: 1rem;
    box-shadow: 0 2px 20px #ffb300;; */
}

p{
    color: rgb(255, 0, 221);
}
.card {
    border-radius: 1rem;
    box-shadow: 0 2px 20px rgb(255, 179, 0);
}
.expenses-filter{
    color: white;
    padding: 0 1rem;
}

.expenses-filter__control{
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 1rem 0;
}

.expenses-filter label{
    font-weight: bold;
    margin-bottom: 0.5rem;

}

.expenses-filter select{
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
    /* background-color: rgba(255, 255, 255, 1); */
}
.expense-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0.5rem;
    margin: 1rem 0;
    background-color: rgb(0, 0, 0);
    color: rgba(255, 0, 221, 0.871);
    box-shadow: 0 2px 12px #ff00ddde;
    border-radius: 1rem;
  }
  
  .expense-item__description {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    -webkit-flex-flow: column-reverse;
            flex-flow: column-reverse;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex: 1 1;
            flex: 1 1;
  }
  
  .expense-item h2 {
    color: #3a3a3a;
    font-size: 1rem;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 0 1rem;
    color: white;
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #ffb300;
    /* background-color: #40005d; */
    border: 1px solid #ff00ddde;
    padding: 0.5rem;
    border-radius: 12px;
    
  }
  
  @media (min-width: 580px) {
    .expense-item__description {
      -webkit-flex-direction: row;
              flex-direction: row;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: flex-start;
              justify-content: flex-start;
      -webkit-flex: 1 1;
              flex: 1 1;
    }
  
    .expense-item__description h2 {
      font-size: 1.25rem;
    }
  
    .expense-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }
.expense-date{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid #ffb300;
    /* color: white; */
    border-radius: 12px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

}

.expense-date__day{
    font-size: 1.5rem;
    font-weight: bold;
}

.expense-date__month{
    font-size: 0.75rem;
    font-weight: bold;
}

.expense-date__year{
    font-size: 0.75rem;
}
.expenses-list{
    list-style: none;
    padding: 0;
}

.expenses-list__fallback{
    color: rgb(255, 0, 221);
text-align: center;
}
.chart-bar{
    width: 1rem;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.chart-bar__inner{
    height: 100%;
    width: 100%;
    border: 1px solid #313131;
    border-radius: 12px;
    background-color: #ffffff;
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.chart-bar__fill{
    background-color: #ffb300;
    width: 100%;
    transition: all 1s ease-out;
}

.chart-bar__label{
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
}
.chart{
    padding: 1rem;
    border-radius: 12px;
    background-color: #ff00dd;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 10rem;
}
.new-expense{
    background-color: #000000;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 2px 12px #ff00ddde;
    color: #ffb300;
}

.new-expense button{
    font: inherit;
    cursor: pointer;
    padding: 0.75rem 1.5rem;
    border: 1px solid #ffb300;
    background-color: #ff00ddde;
    color: white;
    border-radius: 12px;
    margin-right: 1rem;
}

.new-expense button:hover, 
.new-expense button:active{
    background-color: #ffb300;
    border-color: #ff00ddde;
}

.new-expense button.alternative{
    color: #220131;
    border-color: transparent;
    background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
    background-color: #ddb3f8;
}



.new-expense__controls{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
    -webkit-justify-content: center;
            justify-content: center;
}

.new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}

.new-expense__control input{
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 2px solid #ffb300;
    width: 20rem;
    max-width: 100%;
}

.form-display__hidden{
display: none;
}


